.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #211E1E;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-body {
  /*background-color: #211E1E;*/

  background: linear-gradient(
    180deg,
    #211E1E 0%,
    #09093e 80%
  );


  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&family=Kanit&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&family=Playfair+Display:wght@500&display=swap');

.title{
  outline: 1px solid red;

  width: 90%;
  height: auto;

  z-index: 2;
}
.titleTextMajor{
  outline: 1px solid red;


  width: 50%;

  color: #DAD0CF;
  font-family: "Great Vibes";
  font-size:300px;
}
.titleTextMinor{
  outline: 1px solid red;

  width: auto;

  color: #DAD0CF;
  font-family: "Kanit";
}
.moon{

  width:1400px;
  height:1400px;

  outline: 5px solid rgb(255, 0, 0);
  
  /*alignment*/
  position: absolute;
  right: -65px;
  top: 10%;

  z-index: 1;
}



.quotebanner {
  width:100%;
  height:300px;
  
  resize: horizontal;
  color: #484444;
  resize: horizontal;
  
  display:flex;
  justify-content:center;
  align-items:center;
}
.quotetext{
  display: block;
  flex-grow: 0;
  flex-shrink: 1;
  flex-basis: auto;
  align-self: auto;
  order: 0;
  color: #DAD0CF; 
}
.quoteimg{
  width:300px;
  height:300px;
}





.projectbannercontainer {

  width:65%;
  height:auto;

  display:flex;
  justify-content:center;
  align-items:center;
}

.projectbanner {

  width:100%;
  height:300px;
  margin: auto; /*so it's in the middle of the container*/

  border-radius: 2px;

  filter: drop-shadow(16px 20px 20px #000000);

  border-right: 5px solid red;
  /*outline: 5px solid rgb(191, 191, 191, 1);*/

  resize: horizontal;
  /* colour gradient from the bottom left to the top right */
  background: linear-gradient(
    -90deg,
    rgba(249, 64, 64, 0.678) 0%,
    rgba(191, 191, 191, 1) 10%
  );
  
  background-blend-mode: normal;
  resize: horizontal;
  
  display:flex;
  justify-content:center;
  align-items:center;
}

.projecttextcontainer{

  /*with shorter text, they would start to align strangley.
    do not ask me why, or why this fixes it. i do not know.*/
  width: 100%;
  height: 100%;
  align-content: center;
}

.projecttitle{

  display: block;
  margin-right: 10px;
  margin-left: 10px;
  text-align: center;
  
  color: #000000;
}

.projecttext{

  display: block;
  margin-right: 10px;
  margin-left: 10px;
  text-align: center;
  

  color: black;
  font-size: 30px;
}

.projectimg{

  width:290px;
  height:290px;

  /*i could make them smaller then fiddle around with alignment, or i could just do this*/
  /*"it's perhaps the smallest iq solution i could have come up with, but it works f***ing amazing" - michael reeves*/
  outline: 5px solid rgba(191, 191, 191, 1);
  
  /*alignment*/
  display: block;
  margin-right: auto;
  margin-left: 5px;

}

.projectsidetext{

  writing-mode: vertical-rl;
  align-self: left;
  margin: auto;

  color: red;
}

